import { Application, Sprite, Texture, Graphics } from 'pixi.js'
import Victor from 'victor'
import { Button, ScrollBox } from '@pixi/ui'

const workerUrl = 'https://mediaservice.greg-797.workers.dev'
let workerUser = 'gfodor'

const LOCAL_STORAGE_KEY = '__mediaservice__'

const getStoredCaptureForId = (id) => {
  if (!localStorage.getItem(LOCAL_STORAGE_KEY)) {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify({}))
  }

  const captures = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY))

  if (captures[id]) {
    return captures[id]
  }
}

const putStoredCapture = (capture) => {
  const captures = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY))
  captures[capture.id] = capture
  localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(captures))
}

const fetchCapture = async (id) => {
  const url = `${workerUrl}/captures/${id}/meta.json`
  const response = await fetch(url, { method: 'GET', headers: { Authorization: workerUser } })
  const capture = await response.json()

  // HACK, this can go away when the backend is purged
  capture.id = id

  return capture
}

const getCaptureStatus = (capture) => {
  const predictions = ['spriter', 'captioner']

  if (capture.scene) {
    predictions.push('scener')
  }

  let pending = false

  for (const prediction of predictions) {
    if (capture.predictions[prediction]?.done) {
      if (capture.predictions[prediction].status !== 'succeeded') {
        return 'failed'
      }
    } else {
      pending = true
    }
  }

  if (!pending) return 'succeeded'

  // Check if created_at > 1 hour ago, then time it out
  const createdAt = new Date(capture.created_at)
  const now = new Date()

  if (now - createdAt > 1000 * 60 * 60) {
    return 'timeout'
  }

  return 'pending'
}

const isCaptureCompleted = (capture) => {
  return getCaptureStatus(capture) === 'succeeded'
}

const pollCaptureForCompletion = (id, systems) => {
  let firedAvailable = false

  const interval = setInterval(async () => {
    const capture = await fetchCapture(id)

    putStoredCapture(capture)

    if (!firedAvailable) {
      console.log('new capture', capture.id, capture.created_at, capture)

      for (const system of systems) {
        if (system.onCaptureAvailable) {
          system.onCaptureAvailable(capture)
        }
      }
      firedAvailable = true
    }

    if (isCaptureCompleted(capture)) {
      for (const system of systems) {
        if (system.onCaptureCompleted) {
          system.onCaptureCompleted(capture)
        }
      }

      clearInterval(interval)
    }
  }, 5000)
}

const fetchAllCaptures = async (systems) => {
  // const texture = await Assets.load('bunny.png');

  // This creates a texture from a 'bunny.png' image
  // const bunny = new Sprite(texture);
  // const workerUser = 'greg'

  const seenIds = new Set()
  let fetching = false

  const step = async () => {
    if (fetching) return

    fetching = true

    try {
      const response = await fetch(`${workerUrl}/me`, {
        method: 'GET',
        headers: {
          Authorization: workerUser
        }
      })

      const captures = (await response.json()).captures

      for (const { id } of captures) {
        if (seenIds.has(id)) continue

        seenIds.add(id)

        const storedCapture = getStoredCaptureForId(id)

        if (!storedCapture || getCaptureStatus(storedCapture) !== 'succeeded') {
          pollCaptureForCompletion(id, systems)
        } else {
          for (const system of systems) {
            if (system.onCaptureAvailable) {
              system.onCaptureAvailable(storedCapture)
            }

            if (system.onCaptureCompleted) {
              system.onCaptureCompleted(storedCapture)
            }
          }
        }
      }
    } finally {
      fetching = false
    }
  }

  step()
  setInterval(step, 5000)
}

class SpriteDrawerSystem {
  constructor (app, arrangeSystem) {
    this.stage = app.stage
    this.arrangeSystem = arrangeSystem
    this.spriteTouchStartX = null
    this.spriteTouchStartY = null
    this.spriteTouchPointerId = null
    this.spriteTouchSprite = null
    this.spriteIsScene = false
    this.spriteCaptureMap = new Map()
    this.desiredDrawerY = 0

    // HACK
    setTimeout(() => {
      const docWidth = document.body.clientWidth
      const docHeight = document.body.clientHeight

      const drawerHeight = 300
      const elementsMargin = 64
      const scrollPadding = 32
      this.desiredSpriteHeight = drawerHeight - elementsMargin

      const scrollBox = new ScrollBox({
        background: 0x333333,
        items: [],
        width: docWidth,
        height: drawerHeight,
        elementsMargin,
        type: 'horizontal',
        padding: scrollPadding
      })

      scrollBox.position.y = docHeight
      this.desiredDrawerY = docHeight
      this.openDrawerY = docHeight - drawerHeight + elementsMargin - (scrollPadding * 2)

      this.drawerScrollBox = scrollBox

      // HACK
      setInterval(() => {
        this.drawerScrollBox.interactive = true
      }, 100)

      app.stage.addChild(scrollBox)
      const popoutYDelta = 10
      const popoutCancelXDelta = 40

      app.stage.on('touchend', (e) => {
        const drawerIsOpen = this.drawerScrollBox.position.y === this.openDrawerY
        const releasedOutsideOfDrawer = e.data.global.y < this.drawerScrollBox.position.y

        // Close drawer if we tap into the canvas
        if (drawerIsOpen && releasedOutsideOfDrawer) {
          this.closeDrawer()
        }
      })

      scrollBox.on('touchend', (event) => {
        this.spriteTouchStartY = null
        this.spriteTouchStartX = null
        this.spriteTouchPointerId = null
        this.spriteTouchSprite = null
      })

      scrollBox.on('touchmove', (event) => {
        if (this.spriteTouchPointerId !== event.data.pointerId) return

        const dx = event.data.global.x - this.spriteTouchStartX

        if (Math.abs(dx) >= popoutCancelXDelta) {
          this.spriteTouchStartY = null
          this.spriteTouchStartX = null
          this.spriteTouchPointerId = null
          this.spriteTouchSprite = null
        } else if (this.spriteTouchStartY !== null) {
          const dy = event.data.global.y - this.spriteTouchStartY

          if (dy < -popoutYDelta) {
            if (this.deleteMode) {
              for (const [itemId, item] of Object.entries(this.drawerScrollBox.list.children)) {
                if (item === this.spriteTouchSprite) {
                  this.drawerScrollBox.removeItem(itemId)
                  const spriteId = this.spriteCaptureMap.get(this.spriteTouchSprite).id

                  // Perform a delete to /captures/:id
                  fetch(`${workerUrl}/captures/${spriteId}`, {
                    method: 'DELETE',
                    headers: {
                      Authorization: workerUser
                    }
                  })

                  break
                }
              }

              return
            }

            this.drawerScrollBox.resize()

            const sprite = this.spriteTouchSprite
            sprite.visible = false

            setTimeout(() => {
              sprite.visible = true
            }, 1000)

            const capture = this.spriteCaptureMap.get(sprite)
            const globalPos = sprite.getGlobalPosition()
            this.arrangeSystem.addCaptureSprite(globalPos.x, globalPos.y, sprite.scale.x, capture, this.spriteTouchPointerId)

            setTimeout(() => {
              this.desiredDrawerY = docHeight
            }, 100)

            this.spriteTouchStartX = null
            this.spriteTouchStartY = null
            this.spriteTouchPointerId = null
            this.spriteTouchSprite = null
          }
        }
      })
    }, 0)
  }

  onCaptureCompleted (capture) {
    const { id, scene } = capture

    const spritePng = `${workerUrl}/captures/${id}/${scene ? 'scene' : 'sprite'}.png`
    const sprite = Sprite.from(spritePng)
    this.spriteCaptureMap.set(sprite, capture)

    sprite.texture.on('update', () => {
      // TODO atlasing will affect the height calculation here
      const scale = Math.min(1.0, Math.max(0.025, this.desiredSpriteHeight / sprite.texture.height))

      sprite.interactive = true
      sprite.scale.x = scale
      sprite.scale.y = scale

      // If they start dragging it up, hide
      sprite.on('touchstart', (event) => {
        if (this.spriteTouchPointerId !== null) return

        if (this.spriteTouchStartY === null) {
          this.spriteTouchStartX = event.data.global.x
          this.spriteTouchStartY = event.data.global.y
          this.spriteTouchPointerId = event.data.pointerId
          this.spriteIsScene = scene
          this.spriteTouchSprite = sprite
        }
      })

      this.drawerScrollBox.addItem(sprite)
    })
  }

  openDrawer (deleteMode = false) {
    this.desiredDrawerY = this.openDrawerY
    this.stage.removeChild(this.drawerScrollBox)
    this.stage.addChild(this.drawerScrollBox)
    this.deleteMode = deleteMode
  }

  closeDrawer () {
    this.desiredDrawerY = document.body.clientHeight
  }

  tick (delta) {
    if (!this.drawerScrollBox) return

    // Lerp to the desired position using delta
    const currentY = this.drawerScrollBox.position.y
    const desiredY = this.desiredDrawerY

    if (Math.abs(currentY - desiredY) < 0.1) {
      this.drawerScrollBox.position.y = desiredY
      return
    }

    const lerpFactor = 0.2 * delta
    const newY = currentY + ((desiredY - currentY) * lerpFactor)

    this.drawerScrollBox.position.y = newY
  }
}

class ArrangeSystem {
  constructor (app) {
    this.stage = app.stage
    this.stage.interactive = true
    this.stage.buttonMode = true

    this.draggingSprite = null
    this.draggingSpriteInitialPosition = null
    this.draggingSpriteInitialRotation = null
    this.draggingSpriteInitialScale = null
    this.draggingSpriteIsScene = false
    this.sceneSprite = null

    this.touchStarts = new Map()
    this.touches = new Map()

    this.spriteToCapture = new Map()

    this.stage.on('touchend', (e) => {
      this.touchStarts.delete(e.pointerId)
      this.touches.delete(e.pointerId)

      if (this.touches.size === 1 && this.draggingSprite) {
        this.draggingSprite = null
      } else if (this.touches.size === 0 && this.draggingSprite) {
        if (this.draggingSpriteIsScene) {
          if (this.sceneSprite) {
            this.stage.removeChild(this.sceneSprite)
          }

          const docWidth = document.body.clientWidth
          const docHeight = document.body.clientHeight
          this.draggingSprite.anchor.set(0.5, 1.0)
          this.draggingSprite.position.set(docWidth / 2, docHeight)
          this.draggingSprite.scale.set(
            Math.max(
              docHeight / this.draggingSprite.texture.height,
              docWidth / this.draggingSprite.texture.width
            )
          )

          // Send it to the back
          this.stage.removeChild(this.draggingSprite)

          if (this.sceneSprite && this.sceneSprite !== this.draggingSprite) {
            this.spriteToCapture.delete(this.sceneSprite)
          }

          this.stage.addChildAt(this.draggingSprite, 0)
          this.sceneSprite = this.draggingSprite
          this.draggingSpriteIsScene = false
          this.draggingSprite = null
        }
      }
    })

    this.stage.on('touchmove', (e) => {
      if (!this.touchStarts.has(e.pointerId)) return
      if (!this.draggingSprite) return

      const sprite = this.draggingSprite
      const x = e.client.x
      const y = e.client.y

      this.touches.set(e.pointerId, new Victor(x, y))
      const pointerIds = Array.from(this.touches.keys())
      const id1 = pointerIds[0]

      const p1start = this.touchStarts.get(id1)
      const p1 = this.touches.get(id1)

      if (this.touches.size === 1) {
        const dpos = p1.clone().subtract(p1start)
        const newPos = this.draggingSpriteInitialPosition.clone().add(dpos)

        sprite.x = newPos.x
        sprite.y = newPos.y
      } else if (this.touches.size === 2) {
        const id2 = pointerIds[1]
        const p2start = this.touchStarts.get(id2)
        const p2 = this.touches.get(id2)

        const initialDistance = p1start.distance(p2start)
        const currentDistance = p1.distance(p2)

        const deltaDistance = currentDistance - initialDistance

        // TODO this should be fixed up
        const spriteSize = Math.max(sprite.width, sprite.height)
        const pixelsPerUnitSprite = spriteSize / sprite.scale.x / 2
        const newScale = Math.min(1.0, Math.max(0.05, this.draggingSpriteInitialScale + deltaDistance / pixelsPerUnitSprite))

        sprite.scale.x = sprite.scale.y = newScale

        // Rotation is the angle between lines p1start -> p2start and p1 -> p2
        const initialAngle = p1start.clone().subtract(p2start).angle()
        const currentAngle = p1.clone().subtract(p2).angle()

        sprite.rotation = this.draggingSpriteInitialRotation + (currentAngle - initialAngle)

        const center = p1.clone().add(p2).multiply(new Victor(0.5, 0.5))
        const dpos = center.clone().subtract(this.draggingSpriteInitialPosition)
        const newPos = this.draggingSpriteInitialPosition.clone().add(dpos)

        sprite.x = newPos.x
        sprite.y = newPos.y
      }
    })
  }

  async openStory () {
    const captures = [...this.spriteToCapture.values()]
    const nonSceneIds = captures.filter(c => !c.scene).map(c => c.id)
    const sceneId = captures.filter(c => c.scene).map(c => c.id)[0]
    console.log(captures)

    const args = { sprite_ids: nonSceneIds.join(',') }
    if (sceneId) {
      args.scene_id = sceneId
    }

    const storyDiv = document.getElementById('story')
    storyDiv.innerHTML = ''
    storyDiv.style.display = 'block'

    // Call /story with query args using search params
    const qs = new URLSearchParams(args).toString()
    const url = `${workerUrl}/story?${qs}`
    console.log(url)
    const res = await fetch(url)

    // Stream the response body using a Reader
    const reader = res.body.getReader()
    const decoder = new TextDecoder()
    console.log('reading')

    // Print each chunk to the console
    reader.read().then(function processText ({ done, value }) {
      if (done) {
        return
      }

      const st = decoder.decode(value)
      storyDiv.innerHTML += st

      return reader.read().then(processText)
    })
  }

  addCaptureSprite (x, y, scale, capture, beginDraggingWithPointerId) {
    const { id } = capture
    const isScene = capture.scene
    const spritePng = `${workerUrl}/captures/${id}/${isScene ? 'scene' : 'sprite'}.png`
    const sprite = Sprite.from(spritePng)
    sprite.scale.x = scale
    sprite.scale.y = scale
    sprite.interactive = true
    sprite.buttonMode = true
    sprite.x = x
    sprite.y = y
    sprite.anchor.set(0.5)

    if (beginDraggingWithPointerId) {
      this.touchStarts.set(beginDraggingWithPointerId, new Victor(x, y))
      this.touches.set(beginDraggingWithPointerId, new Victor(x, y))

      this.draggingSpriteInitialPosition = new Victor(x, y)
      this.draggingSpriteInitialRotation = 0
      this.draggingSpriteInitialScale = scale
      this.draggingSprite = sprite
      this.draggingSpriteIsScene = isScene
    }

    console.log('add sprite', sprite, capture)
    this.spriteToCapture.set(sprite, capture)

    sprite.on('touchstart', (e) => {
      const pointerId = e.pointerId
      const x = e.client.x
      const y = e.client.y

      this.touchStarts.set(pointerId, new Victor(x, y))
      this.touches.set(pointerId, new Victor(x, y))

      if (this.touches.size === 1) {
        this.draggingSprite = sprite
        this.draggingSpriteInitialPosition = new Victor(this.draggingSprite.x, this.draggingSprite.y)
        this.draggingSpriteInitialRotation = this.draggingSprite.rotation
        this.draggingSpriteInitialScale = this.draggingSprite.scale.x
      }
    })

    this.stage.addChild(sprite)
  }
}

export default async function start (user) {
  const docWidth = document.documentElement.clientWidth
  const docHeight = document.documentElement.clientHeight
  workerUser = user

  const app = new Application({ width: docWidth, height: docHeight, antialias: true })
  document.body.appendChild(app.view)
  app.view.classList.add('game')
  globalThis.__PIXI_APP__ = app

  const arrangeSystem = new ArrangeSystem(app)
  const spriteDrawerSystem = new SpriteDrawerSystem(app, arrangeSystem)

  // Add a blue background

  const bg = new Sprite(Texture.BLACK)
  bg.width = docWidth
  bg.height = docHeight
  app.stage.addChild(bg)

  const buttonView = new Graphics().beginFill('#aaaaaa').drawRoundedRect(0, 0, 64, 64, 64)
  const button = new Button(buttonView)

  button.enabled = true

  button.onDown.connect(() => spriteDrawerSystem.openDrawer())
  button.view.position.x = (docWidth / 2) - 32
  button.view.position.y = docHeight - 96
  app.stage.addChild(button.view)

  fetchAllCaptures([spriteDrawerSystem])

  const delButtonView = new Graphics().beginFill('#ee9999').drawRoundedRect(0, 0, 24, 24, 24)
  const delButton = new Button(delButtonView)
  delButton.enabled = true

  delButton.onDown.connect(() => spriteDrawerSystem.openDrawer(true))
  delButton.view.position.x = 140
  delButton.view.position.y = 3
  app.stage.addChild(delButton.view)

  app.ticker.add(delta => {
    spriteDrawerSystem.tick(delta)
  })

  const storyButtonView = new Graphics().beginFill('#99ee99').drawRoundedRect(0, 0, 24, 24, 24)
  const storyButton = new Button(storyButtonView)
  storyButton.enabled = true

  storyButton.onDown.connect(() => arrangeSystem.openStory())
  storyButton.view.position.x = docWidth - 140
  storyButton.view.position.y = 3
  app.stage.addChild(storyButton.view)

  app.ticker.add(delta => {
    spriteDrawerSystem.tick(delta)
  })
}
