import React, { useState, useRef } from 'react'
import { createRoot } from 'react-dom/client'
import styled from 'styled-components'
import start from './engine'

const Root = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
`

const UserSelect = styled.select`
position: absolute;
top: 5px;
left: 5px;
`

const StoryPanel = styled.div`
  font-family: sans-serif;
  font-size: 14px;
  display: none;
  position: absolute;
  top: 18px;
  left: 18px;
  width: 600px;
  height: 400px;
  white-space: pre-wrap;
  background-color: #eee;
  color: #333;
  font-weight: bold;
  padding: 32px;
  border-radius: 18px;
  overflow: scroll;
`

const App = () => {
  const [selectedUser, setSelectedUser] = useState(localStorage.getItem('__selectUser') || 'gfodor')

  return (
    <Root>
      <UserSelect value={selectedUser} onChange={e => {
        localStorage.setItem('__selectUser', e.target.value)
        setSelectedUser(e.target.value)
        setTimeout(() => document.location.reload(), 1000)
      }}>
          <option value="gfodor">User: Greg</option>
          <option value="rokali">User: Rob</option>
          <option value="geoff">User: Geoff</option>
      </UserSelect>
      <StoryPanel id="story"/>

    </Root>
  )
}

const container = document.getElementById('root')
const root = createRoot(container)
root.render(<App />)
start(localStorage.getItem('__selectUser') || 'gfodor')
